.intro-section {
  width: 100%;
  height: 100%;
  position: relative;
  top: 7rem;
}
.image {
  width: 50%;
  height: 100%;
}
.image img {
  width: 100%;
  object-fit: contain;
  height: 100%;
}
.intro-text {
  width: 50%;
  height: 100%;
}
.intro-text h1 {
  font-size: 3rem;
  text-transform: uppercase;
}

.book-btn {
  background-color: orange;
  color: white;
}
.book-btn:hover {
  background-color: rgb(241, 172, 42);
  color: white;
}

/* Media Queries */
@media only screen and (max-width: 936px) {
  .image {
    display: none;
  }
  .intro-text {
    width: 100%;
    height: 100%;
  }
}
