.rental-section {
  width: 100%;
  height: 100%;
  margin-top: 7rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.heading h3 {
  text-align: center;
  font-size: 3rem;
  font-weight: 700;
  background-image: linear-gradient(
    185deg,
    hsl(321deg 100% 20%) 0%,
    hsl(332deg 100% 23%) 21%,
    hsl(342deg 100% 27%) 30%,
    hsl(353deg 100% 30%) 39%,
    hsl(3deg 100% 33%) 46%,
    hsl(13deg 100% 37%) 54%,
    hsl(24deg 100% 40%) 61%,
    hsl(34deg 100% 43%) 69%,
    hsl(45deg 100% 47%) 79%,
    hsl(55deg 100% 50%) 100%
  );
  -webkit-text-fill-color: transparent;
  /* eslint-disable-next-line */
  -webkit-background-clip: text;
}
.rental-fleet {
  width: 100%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem 0;
}
.car-display {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 50%;
  height: 100%;
}
.car-display img {
  height: 40vh;
  object-fit: contain;
}
.car-name {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 50%;
  height: 100%;
}
.car-name-btn {
  background-image: linear-gradient(
    370deg,
    hsl(0deg 0% 0%) 0%,
    hsl(60deg 6% 9%) 10%,
    hsl(60deg 6% 18%) 20%,
    hsl(54deg 7% 27%) 30%,
    hsl(55deg 7% 37%) 40%,
    hsl(56deg 7% 46%) 50%,
    hsl(56deg 7% 57%) 60%,
    hsl(55deg 7% 68%) 70%,
    hsl(52deg 7% 78%) 80%,
    hsl(60deg 5% 89%) 90%,
    hsl(0deg 0% 100%) 100%
  );
  margin: 0.5rem;
  width: 100%;
  border: none;
  color: white;
  z-index: 2;
  overflow: hidden;
  transition: all 0.2s ease-in-out;
}
.car-name-btn:hover {
  color: white;
  font-weight: 600;
  letter-spacing: 10px;
  text-decoration: overline;
}
.button {
  width: 100%;
  text-align: center;
}
.button button {
  font-size: 1.3rem;
  transition: all 0.3s ease-in-out;
  border: none;
  width: 30vw;
}
.button button:hover {
  width: 100%;
}
/* Media Queries */
@media only screen and (max-width: 991px) {
  .car-display img {
    position: relative;
    height: 33vh;
    left: 60px;
  }
}
/* Another Media Query */
@media only screen and (max-width: 936px) {
  .car-name{
    width: 70%;
  }
  .rental-fleet {
    flex-direction: column;
  }
  .car-display img {
    height: 20vh;
    left: 0;
  }
  .button button {
    font-size: 1.3rem;
    transition: all 0.3s ease-in-out;
    border: none;
    width: 50vw;
  }
  .button button:hover {
    width: 100%;
  }
}