.card-section {
  height: 100%;
  display: block;
  margin: 10rem 5rem 0rem 5rem;
}
.card-text h3 {
  text-align: center;
  font-size: 5rem;
  font-weight: 900;
  background-image: linear-gradient(
    185deg,
    hsl(321deg 100% 20%) 0%,
    hsl(332deg 100% 23%) 21%,
    hsl(342deg 100% 27%) 30%,
    hsl(353deg 100% 30%) 39%,
    hsl(3deg 100% 33%) 46%,
    hsl(13deg 100% 37%) 54%,
    hsl(24deg 100% 40%) 61%,
    hsl(34deg 100% 43%) 69%,
    hsl(45deg 100% 47%) 79%,
    hsl(55deg 100% 50%) 100%
  );
  -webkit-text-fill-color: transparent;
  /* eslint-disable-next-line */
  -webkit-background-clip: text;
}
.card-container {
  display: grid;
  gap: 6rem;
  padding: 1rem;
  grid-template-columns: 1fr 1fr 1fr;
}
.card {
  padding: 0.5rem;
  margin: auto;
  border: none;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.301);
}
.card:hover {
  box-shadow: none;
  cursor: pointer;
}
.card-title{
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  font-weight: 900;
}
/* Media Queries */
@media only screen and (max-width: 300px)  {
  .card-text h3{
    font-size: 3rem;
  }
  .card {
    width: 15rem;
  }
  .card-section {
    margin: 10rem 0;
  }
  .card-container {
    grid-template-columns: 1fr;
  }
}
@media only screen and (max-width: 673px) and (min-width: 300px){
  .card-section {
    margin: 10rem 0;
  }
  .card {
    width: 20rem;
  }
  .card-container {
    grid-template-columns: 1fr;
  }
}
@media only screen and (max-width: 773px) and (min-width: 673px) {
    .card-section {
        margin: 10rem 0;
      }
  .card {
    width: 15rem;
  }
  .card-container {
    grid-template-columns: 1fr 1fr;
  }
}
@media only screen and (max-width: 936px) and (min-width: 773px) {
  .card-container {
    grid-template-columns: 1fr 1fr;
  }
}
@media only screen and (max-width: 1069px) and (min-width: 936px) {
  .card-section {
    margin: 10rem 0rem;
  }
}
