.slider-section {
  margin-top: 8rem;
  overflow: hidden;
  white-space: nowrap;
  align-items: center;
}
.logos {
  display: inline-block;
  animation: 15s slide infinite linear;
  mix-blend-mode: hard-light;
  opacity: 0.8;
}
.logos img {
  height: 15vh;
  margin: 0 2rem;
}
/* Media Queries */
@keyframes slide {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}
