.services-section {
  margin-top: 7rem;
  width: 100%;
  height: 100%;
  margin-bottom: 3rem;
}
.colored-box {
  /* width: 100%; */
  padding: 3rem 0;
  text-align: center;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  border-radius: 0.5rem;
  background: hsla(120, 6%, 90%, 1);
  background: linear-gradient(
    90deg,
    hsla(120, 6%, 90%, 1) 0%,
    hsla(228, 75%, 16%, 1) 100%
  );
  background: -moz-linear-gradient(
    90deg,
    hsla(120, 6%, 90%, 1) 0%,
    hsla(228, 75%, 16%, 1) 100%
  );
  background: -webkit-linear-gradient(
    90deg,
    hsla(120, 6%, 90%, 1) 0%,
    hsla(228, 75%, 16%, 1) 100%
  );
  filter: progid: DXImageTransform.Microsoft.gradient( startColorstr="#E4E7E4", endColorstr="#0A1647", GradientType=1 );
}
.colored-box h3 {
  font-size: 3rem;
  font-weight: 700;
  background-image: linear-gradient(
    185deg,
    hsl(321deg 100% 20%) 0%,
    hsl(332deg 100% 23%) 21%,
    hsl(342deg 100% 27%) 30%,
    hsl(353deg 100% 30%) 39%,
    hsl(3deg 100% 33%) 46%,
    hsl(13deg 100% 37%) 54%,
    hsl(24deg 100% 40%) 61%,
    hsl(34deg 100% 43%) 69%,
    hsl(45deg 100% 47%) 79%,
    hsl(55deg 100% 50%) 100%
  );
  -webkit-text-fill-color: transparent;
  /* eslint-disable-next-line */
  -webkit-background-clip: text;
}
.box-container {
  /* width: 100%; */
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.box {
  width: 30%;
  height: 100%;
  background-color: whitesmoke;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  bottom: 30px;
  border-radius: 0.5rem;
  transition: bottom 0.2s ease-in-out;
}
.box:hover {
  cursor: pointer;
  bottom: 60px;
}
.service-image {
  height: 10vh;
}
.service-image img {
  height: 100%;
  mix-blend-mode: color-burn;
}
.service-text {
  text-align: center;
  height: 70%;
}
/* Media Queries */
@media only screen and (max-width: 936px) {
  .services-section {
    height: 100%;
  }
  .box-container {
    flex-direction: column;
  }
  .box {
    width: 80%;
  }
  .box:hover {
    display: none;
  }
  .colored-box {
    padding: 2rem ;
  }
}
