.body-color {
  background: rgba(255, 255, 255, 0.25);
  box-shadow: 0 2px 10px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border: 1px solid rgba(255, 255, 255, 0.18);
}
.icon {
  height: 10vh;
  font-size: 2rem;
  color: black;
}
.icon i {
  position: absolute;
  left: 55px;
  top: 7px;
}
.nav-text {
  position: absolute;
  bottom: 20px;
  font-weight: bolder;
}
