.about-section {
  width: 100%;
  height: 100%;
  margin-top: 7rem;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.about-image {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
  height: 100%;
  object-fit: contain;
  position: relative;
  left: -7rem;
}
.about-image img {
  height: 50vh;
}
.about-text {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.about-text h3,
span {
  font-size: 3rem;
  font-weight: 700;
  background-image: linear-gradient(
    185deg,
    hsl(321deg 100% 20%) 0%,
    hsl(332deg 100% 23%) 21%,
    hsl(342deg 100% 27%) 30%,
    hsl(353deg 100% 30%) 39%,
    hsl(3deg 100% 33%) 46%,
    hsl(13deg 100% 37%) 54%,
    hsl(24deg 100% 40%) 61%,
    hsl(34deg 100% 43%) 69%,
    hsl(45deg 100% 47%) 79%,
    hsl(55deg 100% 50%) 100%
  );
  -webkit-text-fill-color: transparent;
  /* eslint-disable-next-line */
  -webkit-background-clip: text;
}
/* Media Queries */
@media only screen and (max-width: 936px) {
  .about-section {
    height: 100%;
    width: 100%;
  }
  .about-image {
    opacity: 0.4;
    position: absolute;
  }
  .about-image img {
    height: 40vh;
  }
  .about-text {
    z-index: 1;
    width: 100%;
    font-weight: 600;
  }
}
