.footer-section {
  width: 100%;
  height: 100%;
  color: white;
}
.upper-footer {
  height: 100%;
  padding: 1rem 0;
  position: relative;
  top: 6rem;
  display: flex;
  background-image: url("../footer.jpg");
  border-radius: 1rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.buttons {
  width: 100%;
  height: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.buttons button {
  height: 10vh;
  width: 20vw;
  border-radius: 1rem;
  margin: 0rem 2rem;
  background-color: black;
  color: white;
  border: none;
  padding: 0 2rem;
}
.btnnn {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.upper-footer-icon {
  width: 40%;
  font-size: 2rem;
}
.upper-footer-text {
  margin-top: 10px;
}
.footer {
  height: 40vh;
  width: 100%;
  background-color: black;
  display: flex;
  justify-content: space-around;
  align-items: last baseline;
}
.brand {
  width: 50%;
  padding: 1rem;
}
.social-media {
  width: 50%;
  text-align: end;
  padding: 1rem;
}
.social-media i {
  margin: 0 1rem;
  font-size: 1.5rem;
}
/* Media Queries */
@media only screen and (max-width: 991px) {
  .buttons button {
    margin: 10px 10px;
  }
  .buttons button {
    height: 10vh;
    width: 80%;
  }
}
/* Another Media Queries */
@media only screen and (max-width: 936px) {
  .buttons {
    flex-direction: column;
  }
  .social-media {
    width: 70%;
  }
}
/* Another Query */
@media only screen and (max-width: 300px)  {
    .brand{
      width: 30%;
    }
  }
/* Media Query for Landscape */
@media only screen and (max-width: 936px) and (orientation: landscape) {
  .upper-footer {
    z-index: 5;
  }
  .buttons button {
    height: 15vh;
    padding: 10px;
    width: 70%;
  }
  .footer {
    position: relative;
    top: 3rem;
  }
}
